const Dialog = {
  alert: params => {
    return new Promise(resolve => {
      uni.showModal({
        title: '',
        content: params.message,
        showCancel: false,
        confirmText: params.confirmButtonText ? params.confirmButtonText : '',
        success: res => {
          if (res.confirm) {
            resolve()
          } 
        }
      })
    })
  },

  confirm: params => {
    return new Promise((resolve, reject) => {
      uni.showModal({
        title: '',
        content: params.message,
        showCancel: true,
        cancelText: params.cancelButtonText ? params.cancelButtonText : '',
        confirmText: params.confirmButtonText ? params.confirmButtonText : '',
        success: res => {
          if (res.confirm) {
            resolve()
          } else if (res.cancel) {
            reject()
          }
        }
      })
    })
  }
}

const formartDate = () => {
  const date = new Date()
  const year = date.getFullYear()
  const month = parseInt(date.getMonth()) + 1 > 9 ? parseInt(date.getMonth()) + 1 : '0' + (parseInt(date.getMonth()) + 1)
  const day = date.getDate() > 9 ? date.getDate() : '0' + date.getDate()

  return year + '-' + month + '-' + day
}


/**
 *  
 *
 * @param {Number} timestamp
 */
function fomartTime (timestamp) {
  if (!timestamp) return ''

  const date = new Date(timestamp * 1000)
  const nowDate = new Date()
  const year = date.getFullYear()
  const month = parseInt(date.getMonth()) + 1
  const day = date.getDate() > 9 ? date.getDate() : '0' + date.getDate()
  const hours = date.getHours() > 9 ? date.getHours() : '0' + date.getHours()
  const min = date.getMinutes() > 9 ? date.getMinutes() : '0' + date.getMinutes()

  const nowYear = nowDate.getFullYear()

  if (nowYear !== year) {
    return `${year}${month}${day}`
  }

  if (nowYear === year && date.getMonth() === nowDate.getMonth() && date.getDate() === nowDate.getDate()) {
    return `${hours}:${min}`
  }

  return `${month}${day}`
}


/**
 * 
 */
function generateRichTextNode (text, type) {
  if (!text) return false
  // 
  if (type === 2 || type === 'img') {
    return text
  }

  let tempStr = text
  let list = []
  let leftBracketIndex = tempStr.indexOf('face[')
  let rightBracketIndex = tempStr.indexOf(']')
  let countOfWord = 0
  Array.from(tempStr).map(item => {
    if (item != '[' && item != ']') {
      countOfWord++
    }
  })
  if (leftBracketIndex == -1 || rightBracketIndex == -1 || countOfWord == 0) {//emoji
    return [
      {
        type: 'text',
        content: tempStr
      }
    ]
  }
  while (tempStr.length != 0) {
    leftBracketIndex = tempStr.indexOf('face[')
    rightBracketIndex = tempStr.indexOf(']')
    if (leftBracketIndex == 0) { // [
      rightBracketIndex = tempStr.indexOf(']')
      if (rightBracketIndex == -1) {
        list.push({
          type: 'text',
          content: tempStr
        })
      } else {
        let emojiName = tempStr.slice(0, rightBracketIndex + 1)
        if (emoji[emojiName]) { // emoji
          list.push({
            type: 'emoji',
            content: emoji[emojiName].img
          })
        } else {
          tempStr += emojiName
          list.push({
            type: 'text',
            content: emojiName
          })
        }
        tempStr = tempStr.substring(rightBracketIndex + 1, tempStr.length)
      }
    } else { // [
      if (leftBracketIndex == -1) {// 
        list.push({
          type: 'text',
          content: tempStr.slice(0, tempStr.length)
        })
        tempStr = ''
      } else {
        list.push({
          type: 'text',
          content: tempStr.slice(0, leftBracketIndex)
        })
        tempStr = tempStr.substring(leftBracketIndex, tempStr.length + 1)
      }
    }
  }

  return list
}

