// socket
var socketOpen = false
// socketfunction
var socketClose = false
// socket
var socketMsgQueue = []
// 
var heart = ''
// 
var heartBeatFailCount = 0
// 
var heartBeatTimeOut = null;
// 
var connectSocketTimeOut = null;

var reconnectTimeOut = null
var isReconnect = false
var limit =  0
var webSocket = {
 
  /**
   *  WebSocket 
   * @param {options} 
   *   url          String         wss 
   *   header        Object        HTTP Header , header  Referer
   *   method        String        GETOPTIONS, GET, HEAD, POST, PUT, DELETE, TRACE, CONNECT
   *   protocols    StringArray            1.4.0
   *   success      Function        
   *   fail         Function        
   *   complete      Function        
   */
  ws: null,
  connectSocket (options) {
    socketOpen = false
    socketClose = false
    socketMsgQueue = []
    webSocket.ws = uni.connectSocket({
      url: 'wss://www.bmsapp.cn:8989',
      header: {
          'content-type': 'application/json'
      },
      method: 'GET',
      success: res => {




        if (options) {
          options.success && options.success(res);
        }
      },
      fail: res => {
        console.log('websocket ')
        if (options) {
          // 
          options.fail && options.fail(res);
        }
      }
    })
  },
 
  /**
   *  WebSocket 
   * @param {options} 
   *   data    String / ArrayBuffer        
   *   success    Function        
   *   fail    Function        
   *   complete    Function        
   */
  sendSocketMessage: (options) => {
    if (socketOpen) {
      uni.sendSocketMessage({
        data: options.data,
        success: function(res) {
          if (options) {
            options.success && options.success(res);
          }
        },
        fail: function(res) {
          if (options) {
            options.fail && options.fail(res);
          }
        }
      })
    } else {
      console.log('socket ')
      socketMsgQueue.push(options.data)
    }
  },
  
  reconnect () {
    if (socketOpen && isReconnect) return
    clearTimeout(this.reconnectTimeOut)
    if (limit < 100){
      reconnectTimeOut = setTimeout(() => {
        console.log(`---${limit}`)
        this.connectSocket()
      }, 2000)
      limit = limit + 1
    }
  },
 
  /**
   *  WebSocket 
   * @param {options} 
   *   code    Number        1000 
   *   reason    String        123UTF-8 
   *   fail    Function        
   *   complete    Function        
   */
  closeSocket: function(options) {
    if (connectSocketTimeOut) {
      clearTimeout(connectSocketTimeOut);
      connectSocketTimeOut = null;
    }

    if (!socketOpen) {
      return false
    }

    socketClose = true;
    var self = this;
    self.stopHeartBeat();
    uni.closeSocket({
      success: function(res) {
        console.log('WebSocket ');
        if (options) {
          options.success && options.success(res);
        }
      },
      fail: function(res) {
        if (options) {
          options.fail && options.fail(res);
        }
      }
    })
  },
 
  // 
  onSocketMessageCallback: function(msg) {
  },
 
  // 
  startHeartBeat: function() {
    console.log('socket')
    var self = this;
    heart = 'heart';
    self.heartBeat();
  },
 
  // 
  stopHeartBeat: function() {
    console.log('socket')
    var self = this;
    heart = '';
    if (heartBeatTimeOut) {
      clearTimeout(heartBeatTimeOut);
      heartBeatTimeOut = null;
    }
    if (connectSocketTimeOut) {
      clearTimeout(connectSocketTimeOut);
      connectSocketTimeOut = null;
    }
  },
 
  // 
  heartBeat: function() {
    var self = this;
    if (!heart) {
      return;
    }
    self.sendSocketMessage({
      data: JSON.stringify({
        type: 'ping',
        id: uni.getStorageSync('token')
      }),
      success: function(res) {
        console.log(` ${socketOpen}-----`);
        if (heart) {
          heartBeatTimeOut = setTimeout(() => {
            self.heartBeat();
          }, 9000);
        }
      },
      fail: function(res) {
        console.log('socket');
        if (heartBeatFailCount > 2) {
          // 
          self.connectSocket();
        }
        if (heart) {
          heartBeatTimeOut = setTimeout(() => {
            self.heartBeat();
          }, 9000);
        }
        heartBeatFailCount++;
      },
    });
  }
}

bindAppEvent()


function bindEvent() {
  // WebSocketcallback 
  webSocket.ws.onOpen(function(){
    console.log('WebSocket')
    // function
    if (socketClose) {
      webSocket.closeSocket();
    } else {
      socketOpen = true
      for (var i = 0; i < socketMsgQueue.length; i++) {
        webSocket.sendSocketMessage(socketMsgQueue[i])
      }
      socketMsgQueue = []
      webSocket.startHeartBeat()
      limit = 0
    }
  })
  
  // WebSocket
  webSocket.ws && webSocket.ws.onError(function(res) {
    console.log('WebSocket', res)
    socketOpen = false
    // if (!socketClose) {
    //   webSocket.reconnect()
    // }
  })
  
  // WebSocket
  webSocket.ws && webSocket.ws.onMessage(function(res) {
    console.log('...', res)
    let data = JSON.parse(res.data)
    if (data.type === 'init') {
      webSocket.sendSocketMessage({
        data: JSON.stringify({
          type: 'bind_id',
          id: uni.getStorageSync('token'),
          client_id: data.data.client_id
        })
      })
    }
    webSocket.onSocketMessageCallback(res)
  })

  // WebSocket
  webSocket.ws && webSocket.ws.onClose(function(res) {
    console.log('WebSocket ')
    socketOpen = false
    if (!socketClose) {
      webSocket.reconnect()
    }
  })
  
}

function bindAppEvent() {
  // WebSocket
  uni.onSocketError(function(res) {
    console.log('WebSocket', res)
    socketOpen = false
  })
  
  uni.onSocketOpen(function(){
    console.log('WebSocket')
    // function
    if (socketClose) {
      webSocket.closeSocket();
    } else {
      socketOpen = true
      for (var i = 0; i < socketMsgQueue.length; i++) {
        webSocket.sendSocketMessage(socketMsgQueue[i])
      }
      socketMsgQueue = []
      webSocket.startHeartBeat()
      limit = 0
    }
  })
  
  
  // WebSocket
  uni.onSocketMessage(function(res) {
    console.log('...')
    let data = JSON.parse(res.data)
    if (data.type === 'init') {
      webSocket.sendSocketMessage({
        data: JSON.stringify({
          type: 'bind_id',
          id: uni.getStorageSync('token'),
          client_id: data.data.client_id
        })
      })
    }

    webSocket.onSocketMessageCallback(res)
  })
  
  // WebSocket
  uni.onSocketClose(function(res) {
    console.log('WebSocket ')
    socketOpen = false
    if (!socketClose) {
      webSocket.reconnect()
    }
  })
}
 
module.exports = webSocket;